import { createAsyncThunk, createSlice, isRejectedWithValue } from '@reduxjs/toolkit';
import {  checkUser, createUser,signOut} from './authAPI';
import { updateUser } from '../user/userAPI';
const initialState = {
  loggedInUserToken: null,
  value: 0,
  status: 'idle',
  error:null,

};

export const checkUserAsync = createAsyncThunk(
  'user/checkUser',
  async (logininfo,{isRejectedWithValue}) => {
    try {
      const response = await checkUser(logininfo);
      // The value we return becomes the `fulfilled` action payload
      return response.data;
      
    } catch (error) {
      console.log(error)
      return isRejectedWithValue(error)
      
    }




  }
);
export const createUserAsync = createAsyncThunk(
  'user/createUser',
  async (userData) => {
    const response = await createUser(userData);

    return response.data;
  }
);
// export const updateUserAsync = createAsyncThunk(
//   "user/updateUser",
//   async (update)=>{
//     const response =await updateUser(update)
//     return response.data
//   }

export const signOutAsync = createAsyncThunk(
  "user/signOut",
  async (userId)=>{
    const response =await signOut(userId)
    return response.data
  }
)

export const authSlice = createSlice({
  name: 'user',
  initialState,
  reducers: {
    increment: (state) => {
      state.value += 1;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(createUserAsync.pending, (state) => {
        state.loggedInUserToken = 'loading';
      })
      .addCase(createUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedInUserToken = action.payload;
      })
      .addCase(checkUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(checkUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedInUserToken = action.payload;
      })
      .addCase(checkUserAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.error = action.payload;
      })
      // .addCase(updateUserAsync.fulfilled,(state,action) => {
      //   state.status = "idle"
      //   state.loggedInUserToken = action.payload;
      // })
      // .addCase(updateUserAsync,(state,action) => {
      //   state.status = "loading"
        
      // })
      .addCase(signOutAsync.fulfilled,(state,action) => {
        state.status = "idle"
        state.loggedInUserToken = null ;
      })
      .addCase(signOutAsync,(state,action) => {
        state.status = "loading"
        
      })
  },
});

export const { increment  } = authSlice.actions;

export const selectLogedInUser = (state) => state.auth.loggedInUserToken;
export const selectError = (state) => state.auth.error;

export default authSlice.reducer;
